<template>
  <div name="wall">
    <Navbar />
    <div class="wrapper">
      <Sidebar />
      <div class="container">
        <h1 class="large text-primary">Chatroom</h1>
        <p class="lead">
          <i class="fas fa-comments"></i> Let's have a talk!
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Sidebar from "@/components/Sidebar.vue";
// import io from "socket.io-client";
import UserServices from "../services/UserServices";

export default {
  name: "chatroom",
  data: function() {
    return {
      users: [],
      username: ""
    };
  },
  components: {
    Navbar,
    Sidebar
  },
  beforeRouteEnter(to, from, next) {
    UserServices.getAllUsers().then(res => {
      next(vm => {
        vm.users = res.data;
      });
    });
  },
  methods: {}
};
</script>
